"use client";

import React, { useState, useEffect } from "react";
import Image from "next/image";
import Link from "next/link";
import { useParams } from "next/navigation";
import { motion } from "framer-motion";
import {
    Star,
    ShoppingCart,
    CreditCard,
    Shield,
    ArrowLeft,
    Loader2
} from "lucide-react";
import { Button } from "@/components/ui/Button";
import { Card } from "@/components/ui/Card";
import { formatCurrency } from "@/lib/utils";
import { useCart } from "@/components/cart/CartProvider";
import type { Product } from "@/lib/product-service";

export default function ProductDetailsPage() {
    const { id } = useParams();
    const { addItem } = useCart();
    const [activeImage, setActiveImage] = useState(0);
    const [product, setProduct] = useState<Product | null>(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState("");

    useEffect(() => {
        fetch(`/api/products/${id}`)
            .then(res => {
                if (!res.ok) throw new Error("Product not found");
                return res.json();
            })
            .then(data => setProduct(data))
            .catch(err => setError(err.message))
            .finally(() => setLoading(false));
    }, [id]);

    const handleBuyNow = () => {
        if (!product) return;

        if (product.razorpay_link) {
            // Redirect to Razorpay Payment Link
            window.location.href = product.razorpay_link;
        } else {
            // Fallback: Add to cart and open cart? Or just simulating
            addItem(product);
            alert("This product uses standard checkout. Added to cart.");
        }
    };

    if (loading) return (
        <div className="flex items-center justify-center min-h-[50vh]">
            <Loader2 className="w-8 h-8 animate-spin text-primary" />
        </div>
    );

    if (error || !product) return (
        <div className="container-custom py-20 text-center">
            <h2 className="text-2xl font-bold mb-4">Product Not Found</h2>
            <Link href="/products">
                <Button>Back to Products</Button>
            </Link>
        </div>
    );

    return (
        <div className="container-custom py-12">
            <Link
                href="/products"
                className="inline-flex items-center text-text-secondary hover:text-primary mb-8 transition-colors"
            >
                <ArrowLeft className="w-4 h-4 mr-2" />
                Back to Products
            </Link>

            <div className="grid grid-cols-1 lg:grid-cols-2 gap-12 lg:gap-20">
                {/* Product Images */}
                <motion.div
                    initial={{ opacity: 0, x: -20 }}
                    animate={{ opacity: 1, x: 0 }}
                    className="space-y-4"
                >
                    <div className="relative aspect-square rounded-2xl overflow-hidden bg-surface-elevated shadow-lg">
                        <Image
                            src={product.image_url}
                            alt={product.name}
                            fill
                            className="object-cover"
                            priority
                        />
                    </div>
                </motion.div>

                {/* Product Info */}
                <motion.div
                    initial={{ opacity: 0, x: 20 }}
                    animate={{ opacity: 1, x: 0 }}
                    className="space-y-8"
                >
                    <div>
                        <div className="text-primary font-bold uppercase tracking-wider text-sm mb-2">
                            {product.category}
                        </div>
                        <h1 className="text-4xl lg:text-5xl font-display font-bold mb-4">
                            {product.name}
                        </h1>
                        <div className="flex items-center gap-4 mb-6">
                            <span className="text-3xl font-bold">{formatCurrency(product.price)}</span>
                            <div className="flex items-center text-warning text-sm">
                                <Star className="w-4 h-4 fill-current" />
                                <Star className="w-4 h-4 fill-current" />
                                <Star className="w-4 h-4 fill-current" />
                                <Star className="w-4 h-4 fill-current" />
                                <Star className="w-4 h-4 fill-current" />
                                <span className="ml-2 text-text-secondary">(24 reviews)</span>
                            </div>
                        </div>
                        <p className="text-text-secondary text-lg leading-relaxed">
                            {product.description}
                        </p>
                    </div>

                    {/* Actions */}
                    <div className="flex flex-col sm:flex-row gap-4 pt-6 border-t border-border">
                        <Button size="lg" className="flex-1" onClick={() => addItem(product)}>
                            <ShoppingCart className="w-5 h-5 mr-2" />
                            Add to Cart
                        </Button>
                        <Button size="lg" variant="secondary" className="flex-1" onClick={handleBuyNow}>
                            <CreditCard className="w-5 h-5 mr-2" />
                            Buy Now
                        </Button>
                    </div>

                    {/* Features/Policies (Static for now, could be dynamic) */}
                    <div className="grid grid-cols-1 sm:grid-cols-2 gap-4 pt-8">
                        <div className="flex items-center gap-2 text-sm text-text-secondary">
                            <div className="w-1.5 h-1.5 rounded-full bg-primary" />
                            Premium Material
                        </div>
                        <div className="flex items-center gap-2 text-sm text-text-secondary">
                            <div className="w-1.5 h-1.5 rounded-full bg-primary" />
                            Fast Shipping
                        </div>
                        <div className="flex items-center gap-2 text-sm text-text-secondary">
                            <Shield className="w-4 h-4 text-success" />
                            1 Year Warranty
                        </div>
                    </div>
                </motion.div>
            </div>
        </div>
    );
}
